        截止到2018年底，联盟取得了丰硕的成果，联盟先后在全国29个省（市）百余市县镇，举办各类中小企业上市融资等专题培训和研讨会共计650余期，采取面对面形式，培训中小企业超6万家，培训人数超10万人次。其中对重点培育的3600家中小企业，由联盟专家组采取“一对一”辅导方式；对其中筛选出来的1200家企业提供上市把脉诊断；对最后“大浪淘沙”沉淀下来的“专精特新”300家拟上市企业，特别是高科技制造业，采取精准服务。目前，联盟共计推荐挂牌及上市企业600余家，实现“新三板”挂牌企业500余家，如：力天钨业（833563）、宏岳塑胶（871306）、科达自控（831832）、中科水生（835425）、环宇装备(831130
        )等；中小板、创业板上市企业50余家，如：众生药业（002317）、达实智能（002421）、劲胜精密（300083）、晨光生物（300138）等。直接间接为1000余家中小企业成功融资超100亿元。
        课程规划和教材建设是联盟的生命力。为此，联盟组织业内专家、学者和上市企业家经多年深入研究，并借鉴发达国家资本市场成功做法和经验，改善优化课程产品设计；在此基础上协助编写了《中小企业融资发展白皮书（2017）》、《董秘资格考试教材》、《620家成功在中国境内A股上市企业案例汇编》、《小微企业金融知识普及教育读本》、《中国
        A股上市公司行业、主营产品、财务数据、市值情况汇编》等系列参考教材，为推动中小企业融资和健康发展贡献了力量，受到业界和中小企业的广泛赞誉。
        联盟及承办单位拥有10余项自主知识产权和专利证书。并先后获得部委、地方和行业协会等颁发的荣誉称号：中关村科技园区管理委员会颁发的中关村高科技企业；科技部颁发的国家高新技术企业；北京中关村国际软件孵化协会颁发的中国上市服务机构可信平台；工业和信息化部颁发的国家中小企业公共服务示范平台。
